@font-face {
  font-family: "Almoni Neue Regular";
  src: url("fonts/almoni-neue-aaa-regular.otf") format("opentype");
}

@font-face {
  font-family: "Almoni Neue Light";
  src: url("fonts/almoni-neue-aaa-light.otf") format("opentype");
}

@font-face {
  font-family: "FrankG Regular";
  src: url("fonts/FrankG-Regular.otf") format("opentype");
}

@font-face {
  font-family: "Henri Regular";
  src: url("fonts/HenriTRIAL-Regular.otf") format("opentype");
}

$shineColor: #ee4266;
$shineOpacityColor: rgba(238, 66, 102, 0.3);
$darkColor: #87ea47;
$midDarkColor: #354e5e;
$midColor: #3d5a6c;
$lightColor: #cbf7e4;
$backgroundColor: #fcfafa;
$backgroundDarkColor: #efefef;

.label {
  fill: $lightColor;
  font-family: "Henri Regular";
  font-size: 30pt;
}

.regularDot {
  fill: $darkColor;
  opacity: 0.6;
  cursor: pointer;
  transition: fill 0.3s linear;
}

.active {
  fill: $shineColor;
  cursor: pointer;
  opacity: 1;
}

.searched {
  fill: $shineColor;
  // opacity: 1;
}
