@import "../main.scss";

.scrollBar {
  position: absolute;
  width: 50px;
  bottom: 0;
  right: 0;
  height: 100%;
  background-color: $lightColor;
  cursor: pointer;
  z-index: 2;
}

.playButton {
  position: absolute;
  height: 20px;
  width: 20px;
  top: 25px;
  left: 20px;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
  z-index: 0;
}

.thumb {
  position: relative;
  right: 20%;
  height: 0;
  width: 60%;
  background-color: $midColor;
  cursor: pointer;
}

.marker {
  position: absolute;
  right: 20%;
  height: 5px;
  width: 60%;
  top: 0;
  background-color: $shineOpacityColor;
  animation: fadeIn 0.3s 1;
  animation-timing-function: ease-in-out;
  opacity: 1;
}

#active {
  background-color: $shineColor;
}

.movingDate {
  position: relative;
  height: 100%;
  bottom: 3px;
  left: 8px;
  color: white;
  font-family: "Henri Regular";
  font-size: 12pt;
  z-index: 1;
  direction: ltr;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
