@import "./components/main.scss";

.GraphFrame {
  position: fixed;
  width: 50%;
  height: 45%;
  background-color: $lightColor;
  left: 0;
  bottom: 0;
  cursor: grab;
  &:active {
    cursor: grabbing;
  }
}

.EmotionsFrame {
  position: fixed;
  width: 50%;
  height: 55%;
  background-color: $midColor;
  // background-image: url("./paper.jpg");
  // background-blend-mode: multiply;
  left: 0;
  top: 0;
}

::-webkit-scrollbar {
  width: 0;
}

body {
  overflow: hidden;
  direction: rtl;
}

.hoverBox {
  position: absolute;
  width: 180px;
  top: 50%;
  font-family: "Henri Regular";
  color: black;
  background-color: $backgroundDarkColor;
  transition: right 0.3s linear;
  padding: 10px;
}

.hoverHeader {
  border: 1px solid $midColor;
  border-right: 0;
  border-left: 0;
  padding: 20px 10px 20px 0;
}

.hoverPlace {
  padding: 10px 0;
  direction: ltr;
}

.hoverEmotions {
  padding: 10px 10px 10px 0;
  line-height: 20px;
  font-size: 11pt;
}

.hoverDate {
  padding: 10px 0 0 0;
  direction: ltr;
}

.lineContainer {
  position: fixed;
}

#lineContainer {
  position: relative;
  pointer-events: none;
}
